import React from 'react';
import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import './App.css';
import Scroll from '../components/Scroll';
import ErrorBoundary from '../components/ErrorBoundary';

class App extends React.Component {
    constructor() {
        super()
        this.state = {
            robots: [],
            searchfield: '',
            date: new Date()
        }
    }

    componentDidMount() {
       // fetch("https://members-api.parliament.uk/api/Members/Search")
        fetch("https://jsonplaceholder.typicode.com/users")
        .then( response =>  response.json())
        .then( users =>  this.setState({robots: users}))
        
    }

    onSearchChange = (event) => {
        this.setState({ searchfield: event.target.value }, (state) => console.log(this.state.searchfield))
    }

    render() {
        const {robots, searchfield} = this.state;
        const filteredRobots = robots.filter(robot => {
            return robot.name.toLowerCase().includes(searchfield.toLowerCase())
        })
        return !robots.length ?
            <h1>Loading...</h1> :

            <div className='tc'>
                <h1 className='f1'>RoboFriends</h1>
                <SearchBox searchChange={this.onSearchChange}/>
                <Scroll>
                    <ErrorBoundary>
                        <CardList robots={filteredRobots} />
                    </ErrorBoundary>
                </Scroll>
                <h1>It is {this.state.date.toLocaleDateString()} at {this.state.date.toLocaleTimeString()}</h1>
            </div>
        }
    }

export default App;
